import packageJSON from "../package.json";
const version = packageJSON.version.split(".")[0];
const inDev = process.env.NODE_ENV !== "production";

// Core assets, to preload
let coreAssets = ["./index.js", "./index.css"];

// On install, cache core assets
self.addEventListener("install", function (event) {
  self.skipWaiting();
  event.waitUntil(
    caches.open(version).then((cache) => cache.addAll(coreAssets))
  );
});

// Clear old caches when activated
addEventListener("activate", (event) => {
  event.waitUntil(async function activate() {
    const keys = await caches.keys();
    await Promise.all(keys.map((key) => key !== version && caches.delete(key)));
  });
});

self.addEventListener("fetch", function (event) {
  let request = event.request;
  // Bug fix
  // https://stackoverflow.com/a/49719964
  if (
    event.request.cache === "only-if-cached" &&
    event.request.mode !== "same-origin"
  )
    return;

  // HTML files
  const htmlFile = request.headers.get("Accept").includes("text/html");
  if (inDev || htmlFile) {
    // Network-first
    event.respondWith(
      fetch(request)
        .then(function (response) {
          const copy = response.clone();
          event.waitUntil(
            caches.open(version).then(function (cache) {
              return cache.put(request, copy);
            })
          );
          return response;
        })
        .catch((_error) =>
          caches
            .match(request)
            .then((response) => response || caches.match("/offline.html"))
        )
    );
    return;
  }

  // // CSS & JavaScript
  // // Offline-first
  // // if (
  // //   request.headers.get("Accept").includes("text/css") ||
  // //   request.headers.get("Accept").includes("text/javascript")
  // // ) {
  // console.log("offline-first", request);
  // event.respondWith(
  //   caches.match(request).then(function (response) {
  //     return (
  //       response ||
  //       fetch(request).then(function (response) {
  //         // Return the response
  //         return response;
  //       })
  //     );
  //   })
  // );
  // return;
  // // }

  // // Images
  const imageFile = request.headers.get("Accept").includes("image");
  // // Offline-first
  // if (imageFile) {
  event.respondWith(
    caches
      .match(request)
      .then(function (response) {
        if (response) return response;
        return fetch(request).then(function (response) {
          const copy = response.clone();
          event.waitUntil(
            caches.open(version).then((cache) => cache.put(request, copy))
          );
          return response;
        });
      })
      .catch((error) => console.error(error))
  );
  // }
});
